.authors {
    position: relative;
    width: 90%;
    height: 100%;
    background-color: $white;
    padding: 15px;
    box-shadow: 0 0 30px 0 rgba(82, 63, 105, 0.05);
    max-height: 480px;
    border-radius: 5px;
    margin-bottom: 30px;
    -webkit-overflow-scrolling: touch;

    &:last-child {
        margin-bottom: 0;
    }

    @include breakpoint-lg {
        width: 49.333%;
    }

    @include breakpoint-xl {
        margin-bottom: 0;
        width: 32.333%;
    }

    &__container {
        height: 100%;
        width: 100%;
        max-height: 440px;
        position: relative;
        -webkit-overflow-scrolling: touch;
    }

    &__title,
    &__info,
    &__name {
        font-family: "Poppins", sans-serif;
        font-weight: 500;
        color: #181c32;
        font-size: rem(14)
    }
    &__title {
        font-weight: 600;
        font-size: rem(16);

        @include breakpoint-md {
            font-size: rem(18);
        }
    }
    &__info {
        font-size: rem(13);
        color: #B5B5C3;
    }
    &__list {
        position: relative;
        display: inline-block;
        max-height: 370px;
        height: 100%;
        width: 100%;
        overflow-y: scroll;
        overflow-x: hidden;
        padding: 0;
        list-style: none;
        margin-top: 20px;
        -webkit-overflow-scrolling: touch;
    }
    &__item {
        display: flex;
        margin-bottom: 32px;
        &:last-child {
            margin-bottom: 0;
        }
    }
    &__img {
        background-color: #c9f7f5;
        padding: 8px 5px 0 5px;
        width: 40px;
        height: 40px;
        margin-right: 10px;
        border-radius: 5px;
    }
    &__box {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__box-text {
        display: flex;
        flex-direction: column;
    }

    &__select {
        cursor: pointer;
        padding: 5px;
        height: 40px;
        outline: transparent;
        font-family: "Poppins", sans-serif;
        font-weight: 500;
        border-radius: 0.42rem;
        font-size: rem(13);
        text-transform: capitalize;
        color: $colorIconSelect;
        background-color: #e1f0ff;
        border-color: transparent;
        &:hover {
            color: $white;
            background-color: $colorIconSelect;
            border-color: transparent;
        }
    }
    &__option  {
        border-radius: 0.42rem;
        font-family: "Poppins", sans-serif;
        font-weight: 500;
        text-transform: capitalize;
        color: $colorIconSelect;
        background-color: $white;
        padding: 5px;
        cursor: pointer;
    }
     /* Works on Firefox */
     * {
        scrollbar-width: thin;
        scrollbar-color: rgba(0, 0, 0, 0.322) transparent;
    }

    /* Works on Chrome, Edge, and Safari */
    *::-webkit-scrollbar {
        -webkit-appearance: none;
        width: 4px;
    }

    *::-webkit-scrollbar-track {
        background: transparent;
    }

    *::-webkit-scrollbar-thumb {
        background-color: rgba(0, 0, 0, 0.322);
        border-radius: 20px;
    }
}
